<template>
  <div v-resize="onResize">
    <v-card 
      rounded="lg"
      class="mx-auto"
      :elevation="mobile ? '5' : '5'"
      :outlined="($vuetify.theme.dark) ? true : false"
      :color="$vuetify.theme.dark ? '' : 'white'"
    >
      <v-sheet color="transparent" height="157">
        <div class="text-center caption pa-2 mb-4">Analisis Keterlambatan</div>
        <ChartBar :width="200" :height="99" :chartdata="chartdata" :options="options" :status="status"/>
      </v-sheet>
    </v-card>
  </div>
</template>

<script>
import ChartBar from "@/components/ChartAnalisisKaryawan.vue"
// const ChartBar = () => import (/* webpackChunkName: "chart-bar"*/ '../components/ChartAnalisisKaryawan.vue')
// import api from "@/services/http";
// import { mdbBarChart, mdbContainer } from "mdbvue";
export default {
  props: ['chartdata', 'options','status'],
  components: {
    ChartBar,
  },

  data () {
    return {
      loaded: true,
      windowSize: {x: 0, y: 0},
      mobile:null,
    }
  },
  
  watch:{
    windowSize(){
      if (this.windowSize.x < 500) {
        this.titleClass = "d-none"
        this.mobile = true
      }else{
        this.titleClass = "mr-4"
        this.mobile = false
      }
    },
  },

  mounted(){

  },

  computed: {

  },


  methods: {
    onResize(){
      this.windowSize = { x: window.innerWidth, y: window.innerHeight }
    },
  },
}
</script>